<script>

import { formatDate } from './../../helpers/'

export let avatar = "erick"

export let session = {
    author :  {
        displayName : "Erick Ruano",
        picture : `https://avatars.dicebear.com/4.5/api/bottts/${avatar}.svg`,
    },
    slots : {
        available : 45,
        total : 50
    },
    createdAt : new Date('Jan 29, 2021, 09:00 PM')
}

export let style = "";
</script>

<style>
    .session-container{
        display: flex;
        justify-content: flex-start;
        align-content: center; 
        align-items: center;
        padding: 1em 2em;
        font-size:10px;
        -webkit-box-shadow: 0px 0px 9px 3px rgba(0,0,0,0.1); 
        box-shadow: 0px 0px 9px 3px rgba(0,0,0,0.1);
        border-radius: 1em;
        min-width: 320px;
        width:100%;
    }

    .author{
        margin-right:2em;
        width:8em;
    }

    .details{
        /* padding:1rem; */
        width:100%
    }

    .price{
        font-size: 2em;
        display:flex;
        align-items: center;
        text-align: center;
        /* padding:1rem; */
        box-sizing: border-box;
    }

    .avatar-holder{
        border-radius:50%;
        width:8em;
        height:8em;
        overflow:hidden;
        box-sizing: border-box;
    }

    .avatar-holder img{
        width:100%;
        height:100%;
    }

@media (min-width:801px) {
    .session-container{
        width:calc(50% - 2em);
    }
}
    
@media (min-width:1281px) { 
    .session-container{
        width:calc(50% - 6em);
    }
    .session-container:nth-of-type(odd){
        margin-right:2em;
    }
}

</style>


    <div class="session-container" {style}>
        <div class="author">
            <div class="avatar-holder">
                <img src="{session.author.picture}" alt="{session.author.displayName}'s profile picture" width="100%" height="100%" >
            </div>
        </div>
        <div class="details">
                <h2>Iconic Yoga</h2>
                <p>By {session.author.displayName}</p>
                <p>45 Slots available out of 50</p>
                <p>{formatDate(session.createdAt, 'en-EN')}</p>
        </div>
        <div class="price">$34</div>
    </div>
