<script>

import { formatDate } from './../../helpers/'
import { location, push } from 'svelte-spa-router'


export let logo = "img/logo.png"

export let links = [
    {
        url : "/home",
        display : "Home"
    },
    {
        url : "/sessions",
        display : "Sessions"
    },
    {
        url : "/profile",
        display : "Profile"
    },
    {
        url : "/settings",
        display : "Settings"
    },
    {
        url : "/help",
        display : "Help"
    }
]

export let style = "";

</script>

<style>

    .aside-container{
        display:grid;
        grid-template-columns: 1fr;
        grid-template-rows: 4em 1fr;
        grid-template-areas: "logo" "nav";
        justify-content: flex-start;
        width:100%;
        align-content: center;
        align-items: flex-start;
        box-sizing: border-box;
        padding: 2em 0em 0em 0em;
    }

    .logo-holder{
        grid-area:logo;
        display:flex;
        justify-content:center;
        align-content:center;
        align-items:center;
    }

    
    ul{
          list-style-type: none;
          width:100%;
          padding:0em;
          grid-area: nav;
          display:flex;
          flex-direction:column;
          align-items:flex-start;
          align-content:flex-start;
    }

    ul li{
        height:auto;
        text-align:left;
        margin-bottom:4em;
        box-sizing: border-box;
        cursor:pointer;
        width: 100%;
    }

    ul li.selected{
        border-bottom: solid 4px var(--flio-primary);
    }

    p{
        color: var(--flio-black);
        margin-bottom:0.5rem;
        margin-left:1.5em;
        font-size:20px;
        font-weight:500;
    }
    
    @media (min-width:1281px) {
        /* .aside-container{
            padding: 0em 8em;
        } */
    }
</style>


    <div class="aside-container" {style}>

        <section class="logo-holder">
            <img width="100%" height="auto" src="{logo}" alt="Logo">
        </section>

        <ul>
            {#each links as link, i}
            <li class:selected={$location === link.url} on:click={()=>{ push(`#${link.url}`) }}>
                <p>{link.display}</p>
            </li>
            {/each}
        </ul>
        
        
    </div>
